.tournament-widget-score {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  -ms-flex-direction: row;
      flex-direction: row;
  /* overflow: hidden; */
}

.tournament-widget-score__rank {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  width: 100px;
  height: 100%;
  position: relative;
}

.tournament-widget-score__team {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.tournament-widget-score__team-member {
  height: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border-right: 1px solid rgba(0, 0, 0, 0.15);
}

.tournament-widget-score__team-member:first-child .tournament-widget-score__team-member--name {
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
}

.tournament-widget-score__team-member--avatar {
  height: 100%;
  width: 50px;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.tournament-widget-score__team-member--name {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 24px;
  font-weight: 600;
  padding: 10px;
  opacity: 0.7;
}

.tournament-widget-score__count {
  height: 100%;
  width: 100px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  font-size: 46px;
  font-weight: 800;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  position: relative;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.tournament-widget-score__count.score-increased {
  background: rgba(166, 66, 255, 0.1);
}

.tournament-widget-score__count--value {
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}

.tournament-widget-score__count.score-increased .tournament-widget-score__count--value {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.tournament-widget-score__avatar--img {
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  height: 50%;
  width: 100%;
}

.tournament-widget-score__count--floating-number {
  font-size: 28px;
  font-weight: 800;
  color: #a642ff;
  position: absolute;
  text-shadow: rgba(166, 66, 255, 0.5) 0px 0px 20px;
  top: 50%;
  z-index: 1000;
  left: 50%;
  text-align: center;
  width: 100px;
  height: 100px;
  margin-left: -50px;
  margin-top: -50px;
  -webkit-animation: float-up 1s ease forwards;
          animation: float-up 1s ease forwards;
}

.tournament-widget-score__count--floating-number.aniamte {
  -webkit-animation: float-up 800ms ease-in-out forwards;
          animation: float-up 800ms ease-in-out forwards;
}

@-webkit-keyframes float-up {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20% {
    opacity: 1;
    -webkit-transform: translate3d(0, -10px, 0);
            transform: translate3d(0, -10px, 0);
  }
  800% {
    -webkit-transform: translate3d(0, -50px, 0);
            transform: translate3d(0, -50px, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, -80px, 0);
            transform: translate3d(0, -80px, 0);
    opacity: 0;
  }
}

@keyframes float-up {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  20% {
    opacity: 1;
    -webkit-transform: translate3d(0, -10px, 0);
            transform: translate3d(0, -10px, 0);
  }
  800% {
    -webkit-transform: translate3d(0, -50px, 0);
            transform: translate3d(0, -50px, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, -80px, 0);
            transform: translate3d(0, -80px, 0);
    opacity: 0;
  }
}



