.tournament-widget-stats {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  list-style: none;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  width: 100%;
}

.tournament-widget-stats__item {
  width: 33%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  padding: 15px;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  border-right: 1px solid rgba(0, 0, 0, 0.2);
}

.tournament-widget-stats__item:last-child {
  border-right: none;
}

.tournament-widget-stats__item--label {
  font-size: 20px;
  font-weight: 600;
  opacity: 0.8;
  display: inline-block;
  width: auto;
}

.tournament-widget-stats__item--value {
  display: inline-block;
  width: auto;
  font-weight: 800;
  font-size: 30px;
}

.tournament-widget-stats__item--value,
.tournament-widget-stats__item--label {
  width: 100%;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



